import React, { useState } from "react"; //  useState
import { useLocation } from "react-router-dom";
import { useTheme } from "../../contexts/ThemeProvider";
import {
  Nav,
  Link,
  ToggleButton,
  HamburgerIcon, //   
  MobileMenu, //   
} from "./Navigation.styled"; //     
import { useMediaQuery } from "../../hooks/useMediaQuery"; // ,      

const navItems = [
  { to: "/", label: "" },
  { to: "/sermons", label: "" },
  { to: "/home-groups", label: " " },
  { to: "/sunday-school", label: " " },
  { to: "/seminars", label: "" },
  { to: "/news", label: "" },
];

const Navigation = React.forwardRef(({ showNav }, ref) => {
  const { toggleTheme, mode } = useTheme();
  const location = useLocation();
  const isMobile = useMediaQuery("(max-width: 768px)"); //  breakpoint 'md'
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false); //   -

  const toggleMobileMenu = () => {
    setMobileMenuOpen(prev => !prev);
  };

  return (
    <Nav ref={ref} className={showNav ? "nav-visible" : "nav-hidden"}>
      {isMobile ? (
        <>
          <HamburgerIcon onClick={toggleMobileMenu} isOpen={mobileMenuOpen}>
            <div />
            <div />
            <div />
          </HamburgerIcon>
          {/*    ,   */}
          <Link to="/" style={{ marginRight: "auto", marginLeft: "10px" }}>
            
          </Link>
          <ToggleButton onClick={toggleTheme}>{mode === "light" ? "" : ""}</ToggleButton>
          <MobileMenu isOpen={mobileMenuOpen}>
            {navItems.map(item => (
              <Link
                key={item.to}
                to={item.to}
                className={location.pathname === item.to ? "active" : ""}
                onClick={toggleMobileMenu} //      
              >
                {item.label}
              </Link>
            ))}
          </MobileMenu>
        </>
      ) : (
        <>
          {navItems.map(item => (
            <Link
              key={item.to}
              to={item.to}
              className={location.pathname === item.to ? "active" : ""}
            >
              {item.label}
            </Link>
          ))}
          <ToggleButton onClick={toggleTheme}>{mode === "light" ? "" : ""}</ToggleButton>
        </>
      )}
    </Nav>
  );
});

export default Navigation;
